import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';

import {
  SingleInputContainer,
  SingleInputWrapper,
} from './styles/LiquidityRemoveInput.styles';

import useInput from '../../hooks/useInput';
import { callContract } from '../../utils/KAS';
import { exchangeAddressTable } from '../../constants';

interface LayoutProps {
  children: React.ReactNode;
  liftState: (
    value: string,
    isChange: boolean,
    isDecimalError: boolean
  ) => void;
  numberOfDecimal: number;
}

const LiquidityRemoveInput = ({
  children,
  liftState,
  numberOfDecimal,
}: LayoutProps) => {
  const { id } = useParams();
  const [name, setName] = useState<string>('');
  const [maxBalance, setMaxBalance] = useState<number>(0);
  const {
    tokenBalance,
    isFocus,
    isBlankError,
    isDecimalError,
    isChange,
    setKey,
    setIsFocus,
    changeInput,
  } = useInput(numberOfDecimal);

  useEffect(() => {
    //  
    if (id !== undefined) {
      setName(id);
    }
  }, [id]);

  useEffect(() => {
    // name    
    // maxBalance, numberofDecimal
    // 0. lpToken 
    if (window.klaytn.selectedAddress) {
      callContract({
        contractName: 'Exchange',
        contractAddress: exchangeAddressTable[name],
        methodName: 'balanceOf',
        parameters: [window.klaytn.selectedAddress],
      })
        .then((res) => {
          setMaxBalance(Number((Number(res) / 1000000000000000000).toFixed(6)));
        })
        .catch((e) => {
          // console.log(e);
        });
    }
  }, [name]);

  useEffect(() => {
    liftState(tokenBalance, isChange, isDecimalError);
  }, [liftState, tokenBalance, isChange, isDecimalError]);

  return (
    <SingleInputContainer
      isFocus={isFocus}
      isError={isBlankError || isDecimalError}
    >
      <div>
        <section>
          <label htmlFor="input">{children}</label>
          {children === 'INPUT' && (
            <label htmlFor="input">Max {maxBalance}</label>
          )}
        </section>
        <SingleInputWrapper>
          <section>
            {/* temp */}
            {/* <img /> */}
            <div>KLAY + {name} LPT</div>
          </section>
          <input
            placeholder="0.00"
            id="input"
            autoComplete="off"
            value={tokenBalance}
            onKeyDown={(e) => setKey(e.key)}
            onChange={(e) => changeInput(e)}
            onFocus={() => setIsFocus(true)}
            onBlur={() => setIsFocus(false)}
          />
        </SingleInputWrapper>
      </div>
      {/*   */}
      {isDecimalError && (
        <section>
          Amount must be within {numberOfDecimal} decimal points
        </section>
      )}
      {isBlankError && <section>Required</section>}
    </SingleInputContainer>
  );
};

export default LiquidityRemoveInput;
